	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Tuna Casserole","date":"27 Sep 2004","slug":"/2004-09-27-the-jean-paul-sartre-cookbook/","tags":"Y2004"},"html":"<p><a href=\"http://pvspade.com/Sartre/cookbook.html\" title=\"The Jean-Paul Sartre Cookbook\">The Jean-Paul Sartre Cookbook</a> \"Tuna Casserole. Ingredients: 1 large casserole dish. Place the casserole dish in a cold oven. Place a chair facing the oven and sit in it forever. Think about how hungry you are. When night falls, do not turn on the light.\" (Via <a href=\"http://weblog.burningbird.net/archives/2004/09/26/fighting-failure/\" title=\"\">Burningbird</a>.)</p>"},"next":{"fields":{"title":"Stars","date":"27 Sep 2004","slug":"/2004-09-27-stars/"}},"previous":{"fields":{"title":"Vowe's Law","date":"27 Sep 2004","slug":"/2004-09-27-vowes-law/"}}},{"node":{"fields":{"title":"Stars","date":"27 Sep 2004","slug":"/2004-09-27-stars/","tags":"Y2004"},"html":"<p>I think I have discovered a reason for the poor showing of Germany in recent international studies of education systems. </p>\n<p>We're trying to encourage Christopher to, ah, keep his bed dry at night, with only partial success thus far. Last week I had the idea to reward him with a gold star on his calendar for every successful night. Christopher thought it was a wonderful idea (especially after Mama said he could redeem 3 stars for a present).</p>\n<p>However, none of the stationery shops I checked in Hamburg carried gold star stickers. They had plain white square stickers, or colored dots, but no stars. How can a child feel rewarded at school, at church, or wherever without being able to earn gold stars for attendance, good homework, or whatever? Where is the motivation to succeed? Is the only motivation to avoid mistakes, to avoid red marks? Is there no positive reinforcement?</p>\n<p>While I couldn't find any star stickers, I did find multi-colored star-shaped confetti. We have to supply our own glue, but it's working quite nicely. We've haven't had a damp night since we started awarding stars, and Mama already has to redeem her first present.</p>"},"next":{"fields":{"title":"Spelling mistakes","date":"27 Sep 2004","slug":"/2004-09-27-spelling-mistakes/"}},"previous":{"fields":{"title":"Tuna Casserole","date":"27 Sep 2004","slug":"/2004-09-27-the-jean-paul-sartre-cookbook/"}}},{"node":{"fields":{"title":"Spelling mistakes","date":"27 Sep 2004","slug":"/2004-09-27-spelling-mistakes/","tags":"Y2004"},"html":"<p><a href=\"http://www.crookedtimber.org/archives/002576.html\" title=\"Crooked Timber: Spelling mistakes\">Crooked Timber: Cat Stevens denied entry because of spelling mistake</a> Just because you give the government all your data doesn't make the government smart enough to know how to use it.</p>"},"next":{"fields":{"title":"Mandrakesoft Wins One Million Euros Contract","date":"27 Sep 2004","slug":"/2004-09-27-mandrakesoft-wins-one-million-euros-contract/"}},"previous":{"fields":{"title":"Stars","date":"27 Sep 2004","slug":"/2004-09-27-stars/"}}}],"pathPrefix":"","first":false,"last":false,"index":414,"pageCount":1101}}

